import {
    MAP_INIT,
    GET_MAP
} from '../constants/Map';

const initialState = {
    array: [],
};

export default function page(state = initialState, action) {
    switch (action.type) {
        case MAP_INIT:
            return { ...state, array: action.payload };
        case GET_MAP:{
            console.log(state);
            return state;
        }
        default:
            return state;
    }
}